import React, { useEffect, useState } from "react";
import { GatsbyLocation } from "local-types";

import Layout from "../components/Layout";
import SEO from "../components/Seo";
import { useSiteMetadata } from "../hooks";

interface NotFoundPageProps {
  location: GatsbyLocation;
}

const NotFoundPage: React.FunctionComponent<NotFoundPageProps> = ({
  location,
}): React.ReactElement => {
  const { title: siteTitle } = useSiteMetadata();
  
  const [image, setImage] = useState("https://content.fortune.com/wp-content/uploads/2019/01/boo.jpg");
  const [loading, setLoading] = useState(false);

  const getDoggo = async () => {
	setLoading(true);
	const response = await fetch("https://dog.ceo/api/breeds/image/random")
	const data = await response.json();
	setImage(data.message);
	setLoading(false);
  };

  return (
    <Layout location={location} title={siteTitle}>
      <SEO title="404: Not Found" />
      <h1>404: Page Not Found</h1>
      <p>You just hit a route that doesn&#39;t exist....I still have something for you...</p>
	  
	  <p>click on the button below to see more</p>
	  
	  <button onClick= {getDoggo} >Show More Doggos</button>
	  <br/>
	  <br/>
	  {loading ? <img style={{width: 400,height: 400}} src="https://www.jqueryscript.net/images/jQuery-Ajax-Loading-Overlay-with-Loading-Text-Spinner-Plugin.jpg"/> : <img style={{width: 400,height: 400}} alt="try again.." src={image}/>}
    </Layout>
  );
};

export default NotFoundPage;
